body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/static/media/background-image.e8c7f58e.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    /* border: 1px solid blue; */
    display: flex;
    width: 100%;
    height: 100vh;
    justify-content: space-around;
}


.calculator-app {
    width: 231px;
    margin-top: 35vh;
    height: 302px;
}

.my-weather-app{
    margin-top: 35vh;
    height: 14.2rem;
}

.weather-handler {
    width: 410px;
    position: absolute;
    height: 50px;
    border-radius: 50px 50px 0px 0px;
    background-color: rgba(0, 107, 247, 0.219);
    cursor: pointer;
}

.calendar-app {
    margin-top: 30px;
}
.Calculator {
	width: 230px;
	border: .5px solid black;	
	cursor: pointer;
	box-shadow: 0px 0px 10px 5px rgba(0, 0, 0, 0.4);
}

.Calculator .calculator-output {
	display: flex;
	align-items: center;
	background-color: rgba(54, 53, 53, 0.65);
	color: white;
	width: 100%;
	height: 60px;
}

.Calculator .calculator-output .output-text {
	width: 100%;
	margin: 5px;
	text-align: end;
	font-size: 2.8rem;
}

.Calculator .calculator-row:last-child {
	border-bottom: 1px solid black;
}

.Calculator .calculator-row .custom-button:last-child {
	background-color: orange;
}

.Calculator .calculator-row .custom-button {
	width: 57.5px;
	height: 48px;
	font-size: 1.4rem;
	-webkit-transition: all .3s;
	transition: all .3s;
	background-color: rgba(121, 121, 128, 0.8);
	border: none;
	color: white;
	border-right: .1px solid black;
	border-top: .1px solid black;
}

.Calculator .calculator-row .custom-button:hover {
	background-color: rgb(209, 35, 64);
	color: white;
}

.Calculator .calculator-row .custom-button-3 {
	background-color: rgba(53, 53, 56, 0.8);
}

.Calculator .calculator-row .custom-button-2 {
	width: 115px;
	height: 48px;
	font-size: 1.4rem;
	background-color: rgba(121, 121, 128, 0.8);
	color: white;
	border: none;
	border-top: .1px solid black;
	border-right: .1px solid black;
	border-left: none;
}


.Calculator .calculator-row .custom-button-2:hover {
	background-color: rgb(209, 35, 64);
	color: white;
}
.weather-app {
    /* border: 1px solid blue; */
    width: 400px;
    padding: 5px;
    background-color: rgba(14, 14, 53, 0.95);
    border-radius: 50px;
    box-shadow: 0px 0px 10px 5px rgba(0, 0, 0, 0.4);
}

.weather-app .weather-app-title {
    font-size: 1.4rem;
    margin: 10px 25px 0px;
    color: rgb(255, 255, 255);
}
.form {
    margin: 10px 25px;
    display: flex;
    justify-content: space-between;
}

.form .form-input {
    background-color: rgba(14, 14, 53, 0.75);
    color: rgba(167, 173, 184, 0.9);
    border-radius: 15px;
    height: 1.8rem;
    width: 130px;
    border: 1px solid rgba(116, 169, 255, 0.8);
}

.form button {
    border-radius: 15px;
    background-color: rgb(0, 153, 255);
    border: 1px solid rgb(0, 153, 255);
    color: white;
    cursor: pointer;
    font-weight: bold;
}

.form button:hover {
    background-color: rgb(226, 194, 9);
    color: black;
    border: 1px solid rgb(226, 194, 9);
}

::-webkit-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: rgba(167, 173, 184, 0.9);
    font-size: .8rem;
    text-align: center;
    /* border: 1px solid red; */
    height: 1.6rem;
    margin: 0 5px;
    padding: 0 5px;
}

::-ms-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: rgba(167, 173, 184, 0.9);
    font-size: .8rem;
    text-align: center;
    /* border: 1px solid red; */
    height: 1.6rem;
    margin: 0 5px;
    padding: 0 5px;
}

::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: rgba(167, 173, 184, 0.9);
    font-size: .8rem;
    text-align: center;
    /* border: 1px solid red; */
    height: 1.6rem;
    margin: 0 5px;
    padding: 0 5px;
}
.weather-conditions {
    /* border: 1px solid blue; */
    margin: 5px 20px;
    color: white;
}

.weather-conditions .weather-top {
    /* border: 1px solid blue; */
    display: flex;
    justify-content: space-around;
}

.weather-conditions .weather-top p {
    padding: 0;
    margin: 0;
}

.weather-conditions .weather-temp {
    /* border: 1px solid red; */
    font-size: 4rem;
    display: flex;
    justify-content: center;
}

.weather-conditions .weather-temp p {
    padding: 5px 0;
    margin: 0;
}

.weather-conditions .weather-bottom {
    /* border: 1px solid blue; */
    display: flex;
    justify-content: space-around;
}

.weather-conditions .weather-bottom p {
    padding: 0;
    margin: 0;
}
.calendar-app {
    color: white;
    width: 300px;
    background-image: -webkit-linear-gradient(
        top left,
        rgba(148, 66, 255, 0.9),
        rgba(255, 72, 87, 0.9)
    );
    background-image: linear-gradient(
        to bottom right,
        rgba(148, 66, 255, 0.9),
        rgba(255, 72, 87, 0.9)
    );
    color: white;
    border-radius: 3px 50px 3px 3px;
    box-shadow: 0px 0px 10px 5px rgba(0, 0, 0, 0.4);
}

.calendar-app .calendar {
    width: 100%;
    text-align: center;
}

.calendar-app .calendar .current-day {
    background-color: rgb(0, 81, 255);
    color: white;
    border-radius: 3px;
}

.calendar-app .calendar .day {
    padding: 5px 10px;
}

.calendar-app .label-year {
    cursor: pointer;
}

.calendar-app .editor-year {
    background-color: rgba(107, 107, 107, 0.336);
    width: 50px;
    text-align: right;
    color: white;
}

.calendar-header {
    height: 40px;
}

.nav-month {
    display: flex;
    justify-content: space-between;
    height: 40px;
    align-items: center;
}

.nav-month i{
    font-size: 20px;
    cursor: pointer;
}
